<template>
    <!--用户信息-->
    <div class="qa-form">
        <!--顶部图片-->
        <div class="service">
            <a href="javascript:void(0);">
                <img src="./images/concact_img.png" alt="">
            </a>
        </div>

        <h2>留言咨询 <i>（24小时内获得企业快速回复）</i></h2>
        <div class="user-form">
            <el-form ref="userForm" :model="userform" label-width="80px" :rules="userRules">
                <el-form-item label="姓名">
                    <el-input v-model="userform.name"></el-input>
                </el-form-item>
                <el-form-item label="" class="gender">
                    <el-radio-group v-model="userform.gender">
                        <el-radio :label="0">先生</el-radio>
                        <el-radio :label="1">女士</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="联系方式" prop="phone">
                    <el-input v-model="userform.phone"></el-input>
                </el-form-item>
                <el-form-item label="联系地址">
                    <el-input v-model="userform.address"></el-input>
                </el-form-item>
                <el-form-item label="留言信息">
                    <el-input type="textarea" v-model="userform.msg"></el-input>
                </el-form-item>
                <el-form-item label="是否自愿咨询其他项目：" class="msg">
                    <el-radio-group v-model="userform.desire">
                        <el-radio :label="1">是</el-radio>
                        <el-radio :label="0">否</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item>
                    <el-button type="danger" @click="submitUserForm('userForm')">提交留言</el-button>
                </el-form-item>
            </el-form>
        </div>
        <!--快捷留言-->
        <div class="quick-msg">
            <p>快捷留言</p>
            <ul id="quick-nav" @click="quickMsg">
                <li>我想加盟，请电话联系我。</li>
                <li>具体加盟条件有哪些？</li>
                <li>我想到贵公司考察。</li>
                <li>近期加盟有什么优惠吗？</li>
                <li>贵公司都有哪些品牌呢？</li>
                <li>加盟这个品牌需要多少加盟费呢？</li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        name: "qaForm",
        data() {
            // 验证手机号的方法
            const checkPhone = (rule, value, callback) => {
                console.log(value)
                if (!value) {
                    return callback(new Error('手机号不能为空'));
                } else {
                    const reg = /^[1][3,4,5,6,7,8][0-9]{9}$/;
                    console.log(reg.test(value));
                    if (reg.test(value)) {
                        callback();
                    } else {
                        return callback(new Error('请输入正确的手机号'));
                    }
                }
            };
            return {

                // 用户信息表单
                userform: {
                    name: "",
                    gender: "",
                    phone: "",
                    address: "",
                    msg: "",
                    desire: ""
                },

                // 用户表单验证
                userRules: {
                    phone: [
                        {validator: checkPhone, trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
            // 用户表单提交
            submitUserForm(formName) {
                console.log(this.userform)
                this.$refs[formName].validate((valid) => {
                    // console.log(valid)
                    if (valid) {
                        alert('submit!');
                        // 提交成功后清空表单
                        for (let key in this.userform) {
                            this.userform[key] = ""
                        }
                        console.log(this.userform)
                    } else {
                        alert('error submit!!');
                        return false;
                    }
                })
            },
            // 快捷留言
            quickMsg(e) {
                this.userform.msg = e.target.innerHTML
            }
        },
    }
</script>

<style lang="less">
    /* 用户信息 */

    .qa-form {
        width: 100%;
        padding-bottom: 51px;
        background-color: #fff;
        border: 1px solid #ff0101;
        margin-top: 33px;
        position: relative;
        /* 顶部图片 */

        .service {
            a {
                display: block;
                width: 100%;
                height: 100%;

                img {
                    display: block;
                    width: 100%;
                }
            }
        }

        /*  */

        h2 {
            padding-top: 34px;
            padding-left: 43px;
            font-size: 16px;
            color: #1e2629;

            i {
                font-style: normal;
                font-size: 12px;
            }
        }

        /* 用户表单 */

        .user-form {
            width: 330px;
            margin-top: 19px;
            padding-left: 43px;

            .el-form-item__label {
                text-align: center !important;
            }

            .msg {
                .el-form-item__label {
                    width: 166px !important;
                }
            }

            .el-input__inner {
                height: 30px;
                width: 179px;
            }


            .gender {
                .el-form-item__content {
                    margin-left: 0 !important;
                }
            }

            .el-textarea__inner {
                width: 179px;
                max-height: 98px !important;
                min-height: 98px !important;
                color: #1e2629;
            }

            .el-input__inner {
                color: #1e2629;
            }

            .el-button.el-button--danger {
                position: absolute;
                background-color: #f32222;
            }

        }


        .quick-msg {
            position: absolute;
            width: 257px;
            height: 253px;
            //background-color: pink;
            top: 176px;
            right: 160px;
            padding-left: 33px;
            padding-top: 5px;
            border-left: 1px solid #ddd;

            p {
                width: 100%;
                border-bottom: 1px solid #ddd;
                height: 30px;
                font-size: 14px;
            }

            #quick-nav {
                margin-top: 23px;

                li {
                    font-size: 14px;
                    color: #1e2629;
                    line-height: 30px;
                    cursor: pointer;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;

                    &:hover {
                        color: #ff0c0e;
                    }
                }
            }
        }

    }
</style>
